const IntermediateList =() => {
  const [mode, setMode] = useState(Mode.ShowAll)
  return (
  <>
    <div className='container'>
      <ModeControls setMode = {setMode} mode = {mode} />
      <div id='Page' className='m-3'>
        <Intermediate mode = {mode}/>
      </div>
    </div>
  </>
  )
}

